@import 'components/mixins.scss';

.dialog {
  :global {
    .ant-modal-content {
      border-radius: 8px;
    }
    .ant-modal-body {
      padding: 0;
    }
  }
}

.content {
  padding: 24px 40px 24px 24px;
  :global {
    .cover {
      background-repeat: no-repeat;
      background-size: cover;
      background-position: center center;
      height: 180px;
      margin: -24px -40px 24px -24px;
      border-radius: 8px 0 0 0;

    }
  }
}



.sidebar {
  background-color: #f4f7fc !important;
  border-radius: 0 8px 8px 0;
  padding: 24px;
}

.todo-check {
  display: flex !important;
  align-items: flex-start;
  align-content: flex-start;
  :global() {
    & > span:first-child {
      display: inline-block;
      position: relative;
      top: 3px;
    }
    & > span:last-child {
      display: inline-block;
      padding-left: 20px;
      padding-right: 0;
      color: #141322;
      font-size: 13px;
    }
  }
}

.button-action {
  border-radius: 6px !important;
  font-size: 12px !important;
  font-weight: bold !important;
  padding: 4px 8px !important;
  color: #141322 !important;
  :global() {
    > i {
      margin-right: 8px;
    }
  }
}

.list-activity {
  :global {
    .ant-list-header {
      padding-top: 0;
      padding-bottom: 8px;
      padding-left: 20px;
      font-size: 12px;
      font-weight: 600;
      letter-spacing: 0.03em;
      color: #7c7c9f;
    }
    .ant-list-item {
      padding: 8px 0;
    }
    .ant-list-item-meta-avatar {
      margin-top: 4px;
      margin-left: 0;
      margin-right: 15px;
    }
    .ant-list-item-meta-content {
      padding-right: 0;
    }
    .ant-avatar {
      width: 30px;
      height: 30px;
    }
    .ant-list-item-meta-title {
      font-size: 13px;
      // font-weight: 700;
      color: #141322;
    }
    .ant-list-item-meta-title b {
      font-weight: 700;
    }
    .ant-list-item-meta-description {
      font-size: 11px;
      color: #7c7c9f;
      font-weight: 500;
    }
  }
}

.gallery {
  margin-top: rem(10);
  :global {
    
    .items {
      margin-left: -10px;
      margin-right: -10px;
    }

    .item {
      // margin-bottom: rem(10);
      // margin-right: rem(10);
      margin: 0 10px 10px 10px;
    }

    .item-content {
      border-radius: 5px;
      overflow: hidden;
      position: relative;
      background-color: transparent;
      height: rem(115);
      margin-bottom: rem(5);
      
      &:hover {
        .item-control {
          z-index: 10;
          opacity: 1;
        }
      }
    
      img {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: auto;
        height: 100%;
      }
    }

    .item-control {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      z-index: -1;
      opacity: 0;
      background-color: rgba($gray-6, 0.5);
      @include transition-slow;
    }
    
    .item-control-container {
      position: absolute;
      top: 50%;
      left: 0;
      right: 0;
      transform: translateY(-50%);
      z-index: 10;
      text-align: center;

      .ant-btn-group > .ant-btn:first-child:not(:last-child) {
        border-top-left-radius: 4px;
        border-bottom-left-radius: 4px;
      }
      .ant-btn-group > .ant-btn:last-child:not(:first-child) {
        border-top-right-radius: 4px;
        border-bottom-right-radius: 4px;
      }
    }

  }

}